
import { ChangeDetectorRef, Component, ElementRef, OnDestroy, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ClientService } from 'app/shared/services/client/client.service';
import { ActionDialogComponent } from '../action-dialog/action-dialog.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { MatTableFilter } from 'mat-table-filter';
import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import * as XLSX from 'xlsx';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';
import { ViewDialogComponent } from '../view-dialog/view-dialog.component';
import { ChargingProfileService } from 'app/shared/services/charging-profile/charging-profile.service';

export class ChargingProfile {
  chargingProfileId: string;
  stack_level:number;
  schedule_id:number;
  schedule_period_id:number;
  charging_profile_name: string;
  validFrom: string;
  validTo: string;
  duration: string;
  startSchedule: string;
  minChargingRate: string;
  startPeriod: string;
  limit: string;
  numberPhases: string;
  ChargingRateUnit: string;
  chargingProfilePurpose: string;
  chargingProfileKind: string;
  recurrencyKind: string;
  status: string;
  statusString: string;
}

/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})
export class ListComponent implements OnInit {
  filterEntity: ChargingProfile;
  filterType: MatTableFilter;
  displayedColumns: string[] = ['charging_profile_name','stack_level', 'chargingProfilePurpose', 'chargingProfileKind','status','action'];
  head = [['Charging Profile Name','Stack Level', 'Charging Profile Kind','Charging Profile Purpose',  'Recurrency Kind','Duration',
    'Number Phase','Min Charging Rate','Charging Rate Unit','Status']];

  dataSource: MatTableDataSource<ChargingProfile>;

  chargingProList: ChargingProfile[];
  localStorage: any;

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  dialogRef: any;
  charProNameSearch = false;
  charProPurSearch = false;
  recuKindSearch = false;
  durationSearch = false;
  numPhasesSearch =false;
  charProfKindSearch = false;
  stack_levelSearch= false;
  statusSearch =false;
  exportedData: any[];
  constructor(private clientService: ClientService, public dialog: MatDialog, private chargingProfileService: ChargingProfileService,
    private snackBar: MatSnackBar,private cdr: ChangeDetectorRef) {
    // Create 100 users
    // const users = Array.from({length: 100}, (_, k) => createNewUser(k + 1));

    // Assign the data to the data source for the table to render
  }

  ngOnInit() {
    this.getChargingProfile();
    this.filterEntity = new ChargingProfile();
    this.filterType = MatTableFilter.ANYWHERE;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getChargingProfile() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.chargingProfileService.getAllChargingProfileList().subscribe(
      (resp: any) => {
        if (resp != null) {
          this.chargingProList = resp.data;
          for (var i = 0; i < this.chargingProList.length; i++) {
            if (this.chargingProList[i].status == 'N') {
              this.chargingProList[i].statusString = 'Inactive';
            } else {
              this.chargingProList[i].statusString = 'Active';
            }
          }
          this.dataSource = new MatTableDataSource(this.chargingProList);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        }
        else {
          this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 3000 })
          return;
        }
      },
      (err) => {

      }
    );
  }

  delete(id) {
    this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
      disableClose: true
    });
    //this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';
    this.confirmDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.chargingProfileService.chargingProfileDelete(id,this.localStorage.userId).subscribe(
          (resp: any) => {
            if(resp.status == true){
              this.getChargingProfile();
              this.snackBar.open(MessageConstant.deleteMessage, 'Dismiss', { duration: 2000 });
              this.cdr.detectChanges();
            }else{
                this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 2000 });
            }
            
          }, (err) => {
              this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 2000 });
          }
        )
      }
      this.confirmDialogRef = null;
    });

  }


  openDialog(action,data): void {
    
    let params = {
      action : action,
      data : data
    }

    const dialogRef = this.dialog.open(ActionDialogComponent, {
      width: '1200px',
      data: params,
      autoFocus: false,
      disableClose: true, 
      panelClass: 'app-action-dialog',   
    });

    dialogRef.afterClosed().subscribe(result => {
      this.getChargingProfile();
      this.cdr.detectChanges();
    });
  }
  viewDialog(action,data) : void{
    let params = {
      action : action,
      data : data
    }

    const dialogRef = this.dialog.open(ViewDialogComponent, {
      width: '1200px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true    
    });

    dialogRef.afterClosed().subscribe(result => {
      
      // this.moderate(result);
      this.getChargingProfile();
    });
  }
  changeVisibility(visibleOption) {
    if (visibleOption == 'charProNameSearch') {
      this.charProNameSearch = !this.charProNameSearch;
      this.filterEntity.charging_profile_name = '';
    }
    if (visibleOption == 'charProfKindSearch') {
      this.charProfKindSearch = !this.charProfKindSearch;
      this.filterEntity.chargingProfileKind = '';
    }
    if (visibleOption == 'charProPurSearch') {
      this.charProPurSearch = !this.charProPurSearch;
      this.filterEntity.chargingProfilePurpose = '';
    }

    if (visibleOption == 'recuKindSearch') {
      this.recuKindSearch = !this.recuKindSearch;
      this.filterEntity.recurrencyKind = '';
    }

    if (visibleOption == 'durationSearch') {
      this.durationSearch = !this.durationSearch;
      this.filterEntity.duration = '';
    }
    if (visibleOption == 'numPhasesSearch') {
      this.numPhasesSearch = !this.numPhasesSearch;
      this.filterEntity.numberPhases = '';
    }
    if (visibleOption == 'stack_levelSearch') {
      this.stack_levelSearch = !this.stack_levelSearch;
      this.filterEntity.stack_level = null;
    }
    if (visibleOption == 'statusSearch') {
      this.statusSearch = !this.statusSearch;
      this.filterEntity.statusString = null;
    }
  }
  tableExport() {
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var chargingProf = this.dataSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = {
        'Charging Profile Name': chargingProf.charging_profile_name,'Stack Level': chargingProf.stack_level, 'Charging Profile Kind': chargingProf.chargingProfileKind,
        'Charging Profile Purpose': chargingProf.chargingProfilePurpose, 'Recurrency Kind': chargingProf.recurrencyKind, 'Duration': chargingProf.duration,
        'Number Phases': chargingProf.numberPhases, 'Min Charging Rate': chargingProf.minChargingRate, 'Charging Rate Unit': chargingProf.ChargingRateUnit,
        'Valid From': chargingProf.validFrom, 'Valid To': chargingProf.validTo, 'Start Schedule': chargingProf.startSchedule,'Status': chargingProf.statusString
      };
      this.exportedData.push(data);
    }
    const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
    const workBook: XLSX.WorkBook = XLSX.utils.book_new();

    XLSX.utils.book_append_sheet(workBook, workSheet, 'Charging Profile List');
    XLSX.writeFile(workBook, 'Charging Profile.xlsx');
  }

  pdfExport() {
    var doc = new jsPDF({ // set needed dimensions for any elemen

    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var chargingProf = this.dataSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = [chargingProf.charging_profile_name,chargingProf.stack_level, chargingProf.chargingProfileKind, chargingProf.chargingProfilePurpose,
      chargingProf.recurrencyKind,chargingProf.duration,chargingProf.numberPhases,chargingProf.minChargingRate,chargingProf.ChargingRateUnit,
      chargingProf.statusString]; //chargingProf.validFrom,chargingProf.validTo,chargingProf.startSchedule,

      this.exportedData.push(data);
    }
    var img = new Image();
    var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
    imgRight.src = srcSparkEV;
    var createdDate = new Date().toLocaleString(('en-GB'));
    img.src = src;
    doc.setFontSize(11);
    doc.setTextColor(100);
    doc.setFontSize(18);

    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla', fillColor: [11, 148, 68] },
      styles: {
        cellPadding: 1,
        fontSize: 8,

      },

      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage: data => {
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);

        doc.text('Charging Profile List Report', doc.internal.pageSize.getWidth() / 2, 11, { align: 'center' });

        doc.setTextColor(211, 211, 211);
        doc.setFontSize(60);
        doc.setGState(doc.GState({ opacity: 0.6 }));
        doc.text('Confidential/Not for Public use', 25, 285, null, 60,);
        doc.setGState(doc.GState({ opacity: 1 }));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:' + createdDate, 15, 18);

        doc.text('Generated By:' + this.localStorage.f_Name, 195, 18, { align: 'right' });
        doc = this.addWaterMark(doc);
      }
    })

    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow')

    // below line for Download PDF document  
    doc.save('Charging Profile List report.pdf');

  }
  addWaterMark(doc) {
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
    doc.setFontSize(10);
    //Print Page 1 of 4 for example

    doc.text('Page ' + String(i) + ' of ' + String(newpage), 210 - 20, 297 - 10, null, null, "right")
    return doc;

  }



}

