import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidatorFn,AbstractControl} from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { DateService } from 'app/shared/services/utility/date.service';
import {Observable} from 'rxjs';
import {map, startWith,tap} from 'rxjs/operators';
import {MessageConstant} from 'app/shared/utils/message.constant';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { DatePipe } from '@angular/common';


@Component({
  selector: 'app-addItem-dialog',
  templateUrl: './addItem-dialog.component.html',
  styleUrls: ['./addItem-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations,
  providers:[DatePipe]
})


export class AddItemDialogComponent implements OnInit {
 

  form: FormGroup;
  localStorage : any;
  status = false;
  valuedilog = {
    seat1: undefined,
    seat2: undefined,
    totelSec:undefined
  }
 //totelSec:any;

  
  @ViewChild("toggleElement") ref: any;
 
  
  //messageConstant = MessageConstant;
  constructor(private fb: FormBuilder,  private dateService : DateService, private snackBar: MatSnackBar,
    public dialogRef: MatDialogRef<AddItemDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: MtxDialogData, private datePipe: DatePipe,) {}

  ngOnInit() {
    this.initialize();
  }

  initialize(){
    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));
    //this.params = this.data;
    //this.isEditMode = this.params.action == 'CREATE' ? false : true;

    this.form = this.fb.group({
      hours: ['', [Validators.pattern(/^[0-9]*$/),Validators.max(24),Validators.min(0)]],
      minute: ['', [Validators.required, Validators.pattern(/^[0-9]*$/),]],
      
    });
    this.form.get('hours').setValue(0);
    this.form.get('minute').setValue(0);

  
  }
  
  close() {
    this.dialogRef.close();
  }
  closeActionPanel(){
    this.dialogRef.close();
  }

  seat1Modified(event): void {
      let minute = 60 * event.value.key;
      let second = 60 * event.value.key;
      //this.form.get('minute').setValue(minute);
      //value.seat2 = minute ;
      event.totelSec = minute + second;
    
  }

  addSeconds(){
    let hours = this.form.get('hours').value;
    let minute = this.form.get('minute').value;
    var totalSec =  ( ( (hours *60) + minute) *60 );
    this.dialogRef.close(totalSec);
  }

}





