import { Component, OnInit, Inject, ElementRef, ViewChild, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidatorFn, AbstractControl, FormControl, FormArray } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { DateService } from 'app/shared/services/utility/date.service';
import { Observable } from 'rxjs';
import { map, startWith, tap } from 'rxjs/operators';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { DatePipe } from '@angular/common';
import { ChargingProfileService } from 'app/shared/services/charging-profile/charging-profile.service';
import { AddItemDialogComponent } from '../addItem-dialog/addItem-dialog.component';

export interface ChargingProfileSchedule{
  id:number;
  charging_schedule_id:number;
  scheduleData:ChargingSchedulePeriod[]
  status:string;
  created_by:number;
  modifyby:number;
}
export interface ChargingSchedulePeriod{
  id:number;
  start_period:number;
  limit:number;
  numberof_phase_id:number;
}


function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}


@Component({
  selector: 'app-view-dialog',
  templateUrl: './view-dialog.component.html',
  styleUrls: ['./view-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations,
  providers: [DatePipe]
})

export class ViewDialogComponent implements OnInit {

  selectedIndex = 0;
  isFirstTab = true;
  isSecondTab = true;
  chargingProfId: any;
  charProf_forms: FormGroup;
  localStorage: any;
  charProfPurpose: any;
  recurKind: any;
  profileKind: any;
  params: any;
  filteredOptionsCharProfPurp: Observable<any[]>;
  filteredOptionsRecurKind: Observable<any[]>;
  filteredOptionsProfileKind: Observable<any[]>;
  public date: moment.Moment;
  public disabled = false;
  public showSpinners = true;
  public showSeconds = true;
  public touchUi = false;
  public enableMeridian = false;
  minDate = new Date();
  public maxDate: moment.Moment;
  public stepHour = 1;
  public stepMinute = 1;
  public stepSecond = 1;
  scheduleTab: boolean = false;
  periodTab: boolean = false;
  isEditMode = false;
  status = true;
  tempDate = new Date();
  currDate: any = new Date();
  @ViewChild("toggleElement") ref: any;
  //messageConstant = MessageConstant;
  charSchud_forms: FormGroup;
  charRateUnit: any;
  @ViewChild("toggleElement2") ref2: any;
  filteredOptionsCharRate: Observable<any[]>;
  char_ProfileID: any;
  sched_tab: boolean = false;
  status2 = true;

  schPerio_forms: FormGroup;
  num_Phases: any;
  @ViewChild("toggleElement3") ref3: any;
  status3 = true;
  filteredOptionsNumPhases: Observable<any[]>;
  menuArray: any;
  schu_PeriodData:any;
  char_SchudData:any;
  SchPeriodStatus:string;
  cancleBtn = false;

  constructor(private fb: FormBuilder, private chargingProfileService: ChargingProfileService,
    private dateService: DateService, private snackBar: MatSnackBar,
    public dialogRef: MatDialogRef<ViewDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: MtxDialogData, private datePipe: DatePipe,
    public dialog: MatDialog,private cdr: ChangeDetectorRef) { }

  ngOnInit() {
    this.initialize();
  }

  initialize() {

    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.menuArray  = JSON.parse( localStorage.getItem('activeMenuItems'));
    //const fgroup = this.createAddPerForm();
    this.params = this.data;
    this.chargingProfId = this.params.data.chargingProfileId;
    this.isEditMode = this.params.action == 'CREATE' ? false : true;

    this.charProf_forms = this.fb.group({
      stack_level: ['', [Validators.required, Validators.pattern(/^[0-9]*$/)]],
      char_proName: ['', [Validators.required, Validators.pattern(/^[a-zA-Z0-9]*$/)]],
      char_profKind: ['', [autocompleteObjectValidator(), Validators.required]],
      charg_profPur: ['', [autocompleteObjectValidator(), Validators.required]],
      recur_kind: ['', [autocompleteObjectValidator(), Validators.required]],
      toDate: [''],
      fromDate: [''],
      status: this.status == true ? 'Y' : 'N'
    });

    this.charSchud_forms = this.fb.group({
      duration: ['', [Validators.required, Validators.pattern(/^[0-9]*$/)]],
      charging_rate_unit: ['', [autocompleteObjectValidator(), Validators.required]],
      start_schedule: [new Date()],
      charge_rate: ['', [Validators.required, Validators.pattern(/^[0-9]*$/)]],
      status2: this.status2 == true ? 'Y' : 'N'
    });

    this.schPerio_forms = this.fb.group({
      periodForm : this.fb.array([]),
      status3: this.status3 == true ? 'Y' : 'N'
     
    });
    if (this.isEditMode) {
      this.getChargingProgilePurpose();
      this.getChargingRecurrencyKind();
      this.getChargingProfileKind();
      this.assignValuesCharProfile(this.params);
      this.assignValuesCharSchedule(this.params);
      this.getChargingRateUnit();
      //this.assignValuesCharPeriod(this.params);
      this.getNum_Phases();
    } 

  }

  createAddPerForm() {
    return this.fb.group({
      id: new FormControl(''),
      start_period: new FormControl('',[Validators.required, Validators.pattern(/^[0-9]*$/)]),
      limit: new FormControl('',[Validators.required, Validators.pattern(/^[0-9]*$/)]),
      num_phases: new FormControl(''),
    });
  }
  close() {
    this.dialogRef.close();
  }
  closeActionPanel() {
    this.dialogRef.close();
  }
  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }
  onTabChange(event) {
    this.selectedIndex = event.index;
  }
  assignValuesCharProfile(params) {
    this.charProf_forms.get('stack_level').setValue(params.data.stack_level);
    this.charProf_forms.get('char_proName').setValue(params.data.charging_profile_name);
    this.charProf_forms.get('toDate').setValue(params.data.validTo);
    this.charProf_forms.get('fromDate').setValue(params.data.validFrom);
    this.status = params.data.status == 'Y' ? true : false;
  }
  assignValuesCharSchedule(params) {
    this.getScheduleByProfileId(params.data.chargingProfileId);
 }

  
  updateStatus(event) {
    this.status = event.checked;
    this.ref.checked = this.status;
  }
  updateCharProfile() {
    this.isFirstTab = false;
    this.selectedIndex += 1;
  }

  getChargingProgilePurpose() {
    this.chargingProfileService.getChargingProfilePurpose().subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.charProfPurpose = resp.data;
          if (this.isEditMode) {
            const toSelect = this.charProfPurpose.find(c => c.value == this.params.data.chargingProfilePurpose);
            this.charProf_forms.get('charg_profPur').setValue(toSelect);
          }
          this.filteredOptionsCharProfPurp = this.charProf_forms.controls.charg_profPur.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.value),
            map(name => name ? this._filterCharProfPurpose(name) : this.charProfPurpose)
          );

        } else {
          this.snackBar.open(MessageConstant.statusFalse, 'Dismiss', { duration: 5000 });
        }
      }, (err) => {
        ////
      }
    );

  }
  private _filterCharProfPurpose(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.charProfPurpose.filter(option => option.value.toLowerCase().includes(filterValue));
  }
  displayCharProfPur(item: any): string {
    return item && item.value ? item.value : '';
  }

  getChargingRecurrencyKind() {
    this.chargingProfileService.getRecurrencyKind().subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.recurKind = resp.data;
          if (this.isEditMode) {
            const toSelect = this.recurKind.find(c => c.value == this.params.data.recurrencyKind);
            this.charProf_forms.get('recur_kind').setValue(toSelect);
          }
          this.filteredOptionsRecurKind = this.charProf_forms.controls.recur_kind.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.value),
            map(name => name ? this._filterCharRecurKind(name) : this.recurKind)
          );

        } else {
          this.snackBar.open(MessageConstant.statusFalse, 'Dismiss', { duration: 5000 });
        }
      }, (err) => {
        ////
      }
    );
  }
  private _filterCharRecurKind(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.recurKind.filter(option => option.value.toLowerCase().includes(filterValue));
  }
  displayCharRecurKind(item: any): string {
    return item && item.value ? item.value : '';
  }


  getChargingProfileKind() {
    this.chargingProfileService.getChargingProfileKind().subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.profileKind = resp.data;
          if (this.isEditMode) {
            const toSelect = this.profileKind.find(c => c.value == this.params.data.chargingProfileKind);
            this.charProf_forms.get('char_profKind').setValue(toSelect);
          }
          this.filteredOptionsProfileKind = this.charProf_forms.controls.char_profKind.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.value),
            map(name => name ? this._filterCharProfileKind(name) : this.profileKind)
          );

        } else {
          this.snackBar.open(MessageConstant.statusFalse, 'Dismiss', { duration: 5000 });
        }
      }, (err) => {
        ////
      }
    );

  }
  private _filterCharProfileKind(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.profileKind.filter(option => option.value.toLowerCase().includes(filterValue));
  }
  displayCharProfileKind(item: any): string {
    return item && item.value ? item.value : '';
  }


  //tab-2
  updateCharSchedule() {
    if(this.char_SchudData != undefined && this.char_SchudData != null){
        if(this.schu_PeriodData != undefined && this.schu_PeriodData != null){
          this.isSecondTab = false;
          this.selectedIndex += 1;
        }else{
          this.isSecondTab = true
          this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 5000 })
          this.cancleBtn = true
        }
    }else{
      this.isSecondTab = true
      this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 5000 })
      this.cancleBtn = true
    }
    
  }

  getChargingRateUnit() {
    this.chargingProfileService.getChargingRateUnit().subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.charRateUnit = resp.data;
          if (this.isEditMode) {

            if(this.char_SchudData != undefined && this.char_SchudData.charging_rate_unit_id != undefined){
              const toSelect = this.charRateUnit.find(c => c.id == this.char_SchudData.charging_rate_unit_id); //this.char_SchudData.charging_rate_unit_id //this.params.data.ChargingRateUnit
              this.charSchud_forms.get('charging_rate_unit').setValue(toSelect);
            }
          }
          this.filteredOptionsCharRate = this.charSchud_forms.controls.charging_rate_unit.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.value),
            map(name => name ? this._filterCharRateUnit(name) : this.charRateUnit)
          );

        } else {
          this.snackBar.open(MessageConstant.statusFalse, 'Dismiss', { duration: 5000 });
        }
      }, (err) => {
        ////
      }
    );

  }

  private _filterCharRateUnit(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.charRateUnit.filter(option => option.value.toLowerCase().includes(filterValue));
  }
  displayCharRate(item: any): string {
    return item && item.value ? item.value : '';
  }

  updateStatus2(event) {
    this.status2 = event.checked;
    this.ref2.checked = this.status2;
  }

  //tab-3

  updateSchuPeriod() {
    this.dialogRef.close();
  }
  getNum_Phases() {
    this.chargingProfileService.getNumberPhases().subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.num_Phases = resp.data;

        } else {
          this.snackBar.open(MessageConstant.statusFalse, 'Dismiss', { duration: 5000 });
        }
      }, (err) => {
        ////
      }
    );

  }

  updateStatus3(event) {
    this.status3 = event.checked;
    this.ref3.checked = this.status3;
  }

  addItem(){
    const PERform = this.schPerio_forms.get('periodForm') as FormArray;
    //const fgroup = this.createAddPerForm();
    PERform.push(this.createAddPerForm());
  }
  setStartPeriod(i) {  
    const dialogRef = this.dialog.open(AddItemDialogComponent, {
      width: '600px',
      data: {i},
      autoFocus: false,
      disableClose: true,
      panelClass: 'app-action-dialog',
      //position: {top: '0%', left:'10%'}
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result != undefined && result != 0 ){ 
          const formPre = this.schPerio_forms.get('periodForm') as FormArray;
          formPre.controls[i].get('start_period').setValue(result);
          //this.schPerio_forms.get('periodForm')['controls']
          this.cdr.detectChanges();
        }else{
          return;
        }
      
    });
  }
  removevalue(i){
    const formRemove = this.schPerio_forms.get('periodForm') as FormArray
   if (formRemove.length > 1) {
     formRemove.removeAt(i)
   } else {
     formRemove.reset()
   }
 }

 getScheduleByProfileId(charProfileId){
  this.chargingProfileService.getChargerScheduleByProfileId(charProfileId).subscribe(
    (resp:any) => {
      if(resp.status == true){
         this.char_SchudData = resp.data[0];
         this.charSchud_forms.get('duration').setValue(this.char_SchudData.duration);
         this.charSchud_forms.get('start_schedule').setValue(this.char_SchudData.start_schedule);
         //this.charSchud_forms.get('charging_rate_unit').setValue(this.char_SchudData.charging_rate_unit_id);
         this.charSchud_forms.get('charge_rate').setValue(this.char_SchudData.mincharging_rate);
         this.status2 = this.char_SchudData.status == 'Y' ? true : false;
         this.getSchedulePeriodByScheduleId(this.char_SchudData.id);
         this.getChargingRateUnit()
      }
    },(error) =>{

    }
  );
}

 getSchedulePeriodByScheduleId(charSchudId){
  this.chargingProfileService.getChargerSchedulePeriodByScheduleId(charSchudId).subscribe(
    (resp:any) => {
      if(resp.status == true){
          this.schu_PeriodData =  resp.data
          const formPeriod = this.schPerio_forms.get('periodForm') as FormArray;
          for(var i=0; i< this.schu_PeriodData.length; i++){ 
            this.SchPeriodStatus = this.schu_PeriodData[i].status;
             const childForm =   this.createAddPerForm();
             formPeriod.push(childForm);
             formPeriod.controls[i].get('id').setValue(this.schu_PeriodData[i].id);
             formPeriod.controls[i].get('start_period').setValue(this.schu_PeriodData[i].start_period);
             formPeriod.controls[i].get('limit').setValue(this.schu_PeriodData[i].period_limit);
             formPeriod.controls[i].get('num_phases').setValue(this.schu_PeriodData[i].numberof_phase_id);
          }
          this.status3 = this.SchPeriodStatus == 'Y' ? true : false;
      }

    },(error) =>{

    }
  );
}






}





